<template>
  <div :style="{'width':width}">
    <el-form ref="formData" v-model="formData" label-position="top" style="width:100%;text-align: left;">
      <el-form-item>
        <h3>当前赛季:{{formData.seasons[formData.season].label}}</h3>
      </el-form-item>
      <el-form-item label="请输入本赛季最高排名:">
        <el-input v-model="formData.historySeasonRank" type="number" @input="rankControll('historySeasonRank')">
        </el-input>
      </el-form-item>
      <el-form-item label="请输入历史最高排名:" v-if="formData.seasons[formData.season].value > 1">
        <el-input v-model="formData.historyTopRank" type="number" @input="rankControll('historySeasonRank')">
        </el-input>
      </el-form-item>
      <el-form-item label="请输入现在排名:">
        <el-input v-model="formData.start" type="number" @input="rankControll('start')">
        </el-input>
      </el-form-item>
      <el-form-item label="请输入目标排名:">
        <el-input v-model="formData.end" type="number" @input="rankControll('end')">
        </el-input>
      </el-form-item>
      <el-form-item v-if='formData.info' style="text-align: right;" v-html="formData.info">
      </el-form-item>
      <el-form-item style="text-align: right;">
        <el-button @click='rankResult' type='primary'>
          计算
        </el-button>
        <el-button @click='remain' type='primary'>
          矿余
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    name: 'Home',
    data() {
      return {
        total: 42029,
        totalSeason: 10899,
        rank: [
          {
            min: 1,
            max: 10,
            unit: 550,
