            total: 3500
          }, {
            min: 1001,
            max: 2000,
            unit: 5,
            size: 1,
            total: 5000
          }, {
            min: 2001,
            max: 4000,
            unit: 3,
            size: 1,
            total: 6000
          }, {
            min: 4001,
            max: 7999,
            unit: 1,
