            total: 3800
          }, {
            min: 11901,
            max: 12000,
            unit: 80,
            size: 100,
            total: 80
          }, {
            min: 12001,
            max: 15000,
            unit: 45,
            size: 100,
            total: 1350
          }, {
            min: 15001,
            max: 15002,
            unit: 0,
            size: 1,
            total: 0
          }
        ],
        rankSeason: [
          {
            min: 1,
            max: 10,
            unit: 50,
            size: 1,
            total: 500
          },
          {
            min: 11,
            max: 100,
            unit: 10,
            size: 1,
            total: 900
          }, {
            min: 101,
            max: 200,
            unit: 5,
            size: 1,
