          }, {
            min: 201,
            max: 500,
            unit: 3,
            size: 1,
            total: 900
          }, {
            min: 501,
            max: 2000,
            unit: 2,
            size: 1,
            total: 3000
          }, {
            min: 2001,
            max: 3999,
            unit: 1,
            size: 1,
            total: 1999
          }, {
            min: 4000,
            max: 8000,
            unit: 50,
            size: 100,
            total: 2050
          }, {
            min: 8001,
            max: 15000,
            unit: 15,
            size: 100,
            total: 1050
          }, {
            min: 15001,
            max: 15002,
            unit: 0,
            size: 1,
            total: 0
          }
        ],
        formData: {
          seasons: [{
            label: '第一赛季',
            value: 1
          }],
          season: 0,
          historySeasonRank: '',
          historyTopRank: '',
          start: '',
          end: '',
          info: ''
        },
        width: '300px'
      }
    },
    created() {
      console.log(navigator.userAgent)
      if (/Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent)) {
        this.width = '100%'
      }
    },
    methods: {
      rankControll(key) {
        this.formData[key] = parseInt(this.formData[key])
        if (this.formData[key] < 1) {
          this.formData[key] = 1
        } else if (this.formData[key] > 15001) {
          this.formData[key] = 15001
        }
      },
      setHistory() {
        if(this.formData.seasons[this.formData.season].value <= 1){
          this.formData.historyTopRank = ''
        }
        if (this.formData.historySeasonRank && this.formData.start && this.formData.start < this.formData.historySeasonRank) {
          this.formData.historySeasonRank = this.formData.start
        }
        if ((this.formData.historyTopRank && this.formData.historySeasonRank < this.formData.historyTopRank) || !this.formData.historyTopRank) {
          this.formData.historyTopRank = this.formData.historySeasonRank
        }
      },
      rankResult() {
        if (!this.formData.start || !this.formData.end) {
          this.formData.info = '请输入起止排名'
          return
        }
        this.setHistory()
        let start = this.formData.historySeasonRank ? this.formData.historySeasonRank : this.formData.start
        let result = 0
        if (this.formData.historyTopRank != this.formData.historySeasonRank) {
          if (this.formData.historyTopRank > this.formData.end) {
            result += this.calculate(start, this.formData.historyTopRank, true)
            result += this.calculate(this.formData.historyTopRank, this.formData.end, false)
          } else {
            result += this.calculate(start, this.formData.end, true)
          }
        } else {
          result += this.calculate(start, this.formData.end, false)
        }
        this.formData.info = `本次实际提升排名：从 ${start} 名到 ${this.formData.end} 名，获得：${result}宝石`
      },
      remain() {
        if (!this.formData.historySeasonRank) {
          this.formData.info = '请输入历史排名'
          return
        }
        this.setHistory()
        let result = 0
        if (this.formData.historyTopRank != this.formData.historySeasonRank) {
          result += this.calculate(this.formData.historySeasonRank, this.formData.historyTopRank, true)
          result += this.calculate(this.formData.historyTopRank, 1, false)
        } else {
          result += this.calculate(this.formData.historySeasonRank, 1, false)
        }
        let seasonResult = this.calculate(this.formData.historySeasonRank, 1, true)
        this.formData.info = `<p>赛季矿余：${seasonResult}宝石</p><p>竞技场矿余：${result - seasonResult}宝石</p><p>总矿余：${result}宝石</p>`
      },
      calculate(start, end, isSeason) {
        if (start <= end) {
          return 0
        }
        if (start == 15000) {
          start = 14999
        }
        let rank = isSeason ? this.rankSeason : this.rank
        let result = 0
        let startMark = false
        for (let i = 0; i < rank.length; i++) {
          if (!startMark && rank[i].max >= end) {
            result += rank[i].unit * (parseInt((rank[i].max - end) / rank[i].size) + 1)
            startMark = true
          }
          if (startMark) {
            if (end < rank[i].min) {
              if (rank[i].max >= start) {
                result += rank[i].unit * parseInt((start - rank[i].min) / rank[i].size)
                return result
              } else {
                result += rank[i].total
              }
            } else if (start < rank[i].max) {
              result -= rank[i].unit * (parseInt((rank[i].max - start) / rank[i].size) + 1)
              return result
            }
          }
        }
      }
    }
  }
</script>